body {
  margin: 0;
  padding: 0;
  position: relative;
}

.form-thumb {
  text-align: center;
  background-color: rgba(33, 150, 243, .8);
  padding-top: 20px;
  padding-bottom: 20px;
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
}

form {
  border-radius: 5px;
  display: block;
}

a {
  color: #757575;
  text-decoration: none;
  display: block;
}

p {
  margin: 0;
}

.thumb {
  height: 300px;
  width: 100%;
  display: block;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.gallery {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 30px 16px 16px;
  display: grid;
}

.photo-card {
  margin: 10px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  display: flex;
}

.info-item {
  width: 25%;
  text-align: center;
  padding: 10px 5px 5px;
  font-size: 14px;
}

